import * as PIXI from "pixi.js"

/**
 * @class
 */
export class AnimationHelper {

  static loadClip(
    resources: PIXI.IResourceDictionary,
    which: string,
    speed: number,
    scale: number
  ): PIXI.AnimatedSprite {
    const frames: PIXI.Texture[] = [];
    for (const textureId in resources[which].textures) {
      let texture = resources[which]!.textures![textureId];
      frames.push(texture);
    }

    const clip = new PIXI.AnimatedSprite(frames);

    clip.animationSpeed = speed;
    clip.scale.x = scale;
    clip.scale.y = scale;
    clip.anchor.x = 0.5;

    return clip;
  }
}