import * as PIXI from "pixi.js"
import { ObjectBase } from "./object-base"

export class Crosshair extends ObjectBase {

  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super(resources);

    this.addChild(
      new PIXI.Text("x", {
          fill: "#fff"
        } as PIXI.TextStyle
      )
    )
  }
}
