import * as PIXI from "pixi.js"
import { ObjectBase } from "../object-base"
import { Blood } from "../blood"

export class WeaponBase extends ObjectBase {

  protected _reloadTime: number
  protected _isReloading: boolean = false

  protected _power: number
  protected _maxRounds: number
  protected _rounds: number
  protected _sprite?: PIXI.Sprite
  protected _roundsSprite?: PIXI.Sprite

  get rounds() {
    return this._rounds
  }

  get sprite() {
    return this._sprite
  }

  get roundsSprite() {
    return this._roundsSprite
  }

  get isReloading() {
    return this._isReloading
  }

  constructor(
    resources: PIXI.IResourceDictionary,
    power: number,
    rounds: number
  ) {
    super(resources);

    this._power = power
    this._rounds = rounds
    this._maxRounds = rounds
    this._reloadTime = 5

    this.zIndex = 100

    this.x = -5
    this.y = 20
  }

  public fire(): boolean {

    if (
      this._rounds > 0 &&
      !this._isReloading
    ) {
      // muzzle flash
      const muzzleFlash = new Blood(this._resources, 2)

      muzzleFlash.x = 80
      this.addChild(muzzleFlash);

      this._rounds--;

      // todo: add fire sound

      return true
    }

    // todo: add empty clips sound

    return false
  }

  public reload(): boolean {

    this._isReloading = true
    setTimeout(() => {
      this._rounds = this._maxRounds
      this._isReloading = false
    }, this._reloadTime * 1000)

    console.log("reload")
    return true
  }
}