import * as PIXI from "pixi.js"
import { Config } from "../config"
import { ObjectBase } from "./object-base"

export class Beacon extends ObjectBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number,
    height: number
  ) {
    super(resources);

    this._isFacingRight = true

    this.height = height;
    this.width = width;

    const bg = new PIXI.Sprite(
      Config.highlightBeacons ? PIXI.Texture.WHITE : PIXI.Texture.EMPTY
    );

    bg.width = width
    bg.height = height

    if (Config.highlightBeacons) {
      bg.tint = 0xff0000
    }

    this.addChild(bg)
  }

}
