import PIXI from "pixi.js"
import { ActorStates, MathHelper } from "../tools";
import { AnimationBase } from "../objects"
import { TextBubble } from "../objects/text-bubble"
import { Config } from "../config"

/**
 * @class
 */
export class ActorBase extends AnimationBase {

  protected _text?: TextBubble

  // time delta of last sprite flip
  private _lastFlip = 0;

  /**
   *
   * @type {null}
   * @protected
   */
  protected _skin?: string;

  /**
   *
   * @type {number}
   * @protected
   */
  protected _speed = 1;

  /**
   * the objects health
   * @type {number}
   * @protected
   */
  protected _health = 100;

  /**
   * the objects striking power
   * @type {number}
   * @protected
   */
  protected _power = 12;

  /**
   * @type {Symbol}
   * @protected
   */
  protected _state: Symbol = ActorStates.idle;

  /**
   * can we get git again helper
   * @type {boolean}
   * @protected
   */
  protected _canGetHitAgain = true;

  /**
   * @constructor
   */
  constructor(
    resources: PIXI.IResourceDictionary,
    skin: string,
    speed: number
  ) {
    super(resources);

    /**
     * set the actor skin
     * @type {String}
     * @protected
     */
    this._skin = skin;

    /**
     * the objects speed
     * @type {number}
     * @protected
     */
    this._speed = speed;
  }

  /**
   *
   * @returns {number}
   */
  get power() {
    return this._power;
  }

  /**
   *
   * @returns {number}
   */
  get health() {
    return this._health;
  }

  /**
   *
   * @returns {Symbol}
   */
  public get state() {
    return this._state;
  }

  /**
   *
   * @param state
   * @protected
   */
  protected _setState(state: Symbol) {
    console.log(this.constructor.name, "New state:", state);
    this._state = state;
  }

  /**
   *
   * @protected
   */
  protected _idle() {
    // dont go to idle if we are dead
    if (this._state !== ActorStates.died) {
      this._setState(ActorStates.idle)

      this._playClip(
        this._skin + ".stance",
        true
      );
    }
  }

  /**
   *
   * @protected
   */
  protected _died() {
    this._setState(ActorStates.died);

    // remove all velocity
    this._vy = 0;
    this._vx = 0;
    this._vr = 0;

    // play died clip
    this._playClip(
      this._skin + ".died",
      false,
      true
    )
  }

  /**
   * @public
   * @param actor
   * @returns {boolean}
   */
  public hitBy(
    actor: ActorBase
  ) {

    let died = false;
    let objectTookDamage = false;

    if (this._canGetHitAgain) {

      // lower the heal
      this._health -= actor.power;

      objectTookDamage = true;

      // after we got hit we cannot get hit again
      this._canGetHitAgain = false;

      // are we death?
      if (this._health < 0) {

        this._health = 0;

        // yep
        died = true;

        this._died();
      }
      // not died
      else {

        // force the player a little bit back on hit
        this._vx = actor._isFacingRight ? 5 : -5;

        // play the gothit clip
        this._playClip(
          this._skin + ".gothit",
          false
        )
          .then(() => {

            // animation ended!

            // remove the velocity
            this._vx = 0;

            // yep we can get hit again
            this._canGetHitAgain = true;

            // stand up and idle afterwards
            this._idle();
          });
      }
    }

    return {
      died,
      objectTookDamage
    };
  }

  public flip(
    direction?: boolean
  ) {
    this._setIsFacingRight(
      direction !== undefined ? direction : !this._isFacingRight
    )
  }

  /**
   *
   * @param target
   */
  public moveTowards(target: PIXI.Point) {
    const dist = MathHelper.distanceTo(this.position, target);

    let vx = 0;
    let vy = 0;

    if (dist > 1) {
      let angleToTarget = MathHelper.angleBetweenRad(this.position, target);

      vy = Math.sin(angleToTarget) * this._speed;
      vx = Math.cos(angleToTarget) * this._speed;

      // flip player over
      if (vx > 0 && !this._isFacingRight && this._canFlipAgain()) {

        //console.log("Enemy:", "flip left");
        this.flip();
      }
      // flip player over
      else if (vx < 0 && this._isFacingRight && this._canFlipAgain()) {

        //console.log("Enemy:", "flip right");
        this.flip();
      }
    }

    this._vy = vy;
    this._vx = vx;
  }

  /**
   *
   * @returns {boolean}
   * @private
   */
  private _canFlipAgain() {
    let canFlip = true;
    if (Date.now() - this._lastFlip > 200) {
      canFlip = true;
      this._lastFlip = Date.now();
    }

    return canFlip;
  }

  private _logActorCoordinates() {
    if (Config.logActorCoordinates) {
      console.log(
        "x", this.x,
        "real x", this.realx,
        "y", this.y,
        "width", this.width,
        "height", this.height
      )
    }
  }

  protected moved() {
    this._logActorCoordinates()
    if (this._text) {
      this._text.x = this.x - this._text.width / 2
      this._text.y = this.y - 25
    }
  }

  public speak(
    text: string
  ) {
    if (!this._text) {
      this._text = new TextBubble(
        this._resources,
        text, 3,
        () => {
          this.parent.removeChild(this._text!)
          delete this._text
        }
      )
      this._text.zIndex = 80
      this._text.x = this.x - this._text.width / 2
      this._text.y = this.y - 25
      this.parent.addChild(this._text)
    }
  }

  public move(
    maxHeight: number,
    maxWidth: number
  ): boolean {
    return false
  }
}
