import * as PIXI from "pixi.js"
import { Door } from "../objects"
import { MapBase } from "./map-base"

/**
 *
 */
export class Loading extends MapBase {

  private readonly _door: Door

  constructor(
    resources: PIXI.IResourceDictionary,
    width: number,
    done?: (() => void)
  ) {
    super(resources, width);

    const bg = new PIXI.Sprite(PIXI.Texture.WHITE);

    this._door = new Door(
      resources,
      done
    )
    this._door.x = width / 2
    this._door.y = 30

    bg.width = width
    bg.height = 800
    bg.tint = 0x000000

    this._backgroundContainer.addChild(bg)

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    this._foregroundContainer.addChild(this._door);
  }

  public animate(delta: number) {
    this._door.y += delta / 100
    const scale = this._door.scale.x + delta / 20000
    this._door.scale.x = scale
    this._door.scale.y = scale
  }
}