import * as PIXI from "pixi.js"
import { AnimationBase } from "./animation-base"

/**
 *
 */
export class Decale extends AnimationBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    which: string
  ) {
    super(resources);

    const res = this._resources["things.decals"]

    const decale = new PIXI.Sprite(res.textures![which])

    this.addChild(decale)
  }
}