import * as PIXI from "pixi.js"
import { AnimationBase } from "./animation-base"
PIXI.settings.SCALE_MODE = PIXI.SCALE_MODES.NEAREST;

/**
 *
 */
export class TextBubble extends AnimationBase {

  constructor(
    resources: PIXI.IResourceDictionary,
    text: string,
    duration: number,
    done: (() => void)
  ) {
    super(resources);

    const t = new PIXI.Text(
      text, {
        fontSize: "12px",
        fill: "#fff"
      } as PIXI.TextStyle)
    const bg = new PIXI.Sprite(
      PIXI.Texture.WHITE
    );

    bg.width = t.width
    bg.height = t.height
    bg.tint = 0x000000

    this.addChild(bg)
    this.addChild(t)

    setTimeout(() => {
      done()
    }, duration * 1000)
  }
}