import * as PIXI from "pixi.js"
import { WeaponBase } from "./weapon-base"

/**
 *
 */
export class Beretta extends WeaponBase {

  /**
   *
   * @param resources
   */
  constructor(
    resources: PIXI.IResourceDictionary
  ) {
    super(resources, 55, 8);

    const which = "weapons.beretta"

    const res = resources![which]

    // todo: replace with correct skin
    this._sprite = new PIXI.Sprite(
      res.textures!["beretta.png"]!
    );

    this._sprite.scale.x = 0.1
    this._sprite.scale.y = 0.1

    // sprite should be in the back so add it first
    this.addChild(this._sprite);

    this._roundsSprite = new PIXI.Sprite(
      res.textures!["shell.png"]!
    );
  }
}